@import '_global.scss';

body {
  background: black;
  color: $text-primary;
  font-family: Inter, Roboto, 'Helvetica Neue', 'Arial Nova', 'Nimbus Sans', Arial, sans-serif;
  overflow-x: hidden;
}

a, a:link {
  color: $text-primary;
}

.App {
  width: 100%;
  overflow-x: hidden;
}

#nameplate {
  background-color: #fff;
  display: flex;
  align-items: center;
  position: relative;

  #nameplate-content {
    position: absolute;
    left: 0;
    right: 0;
    pointer-events: none;

    h1 {
      margin: 0;
      font-size: 6vw;
      color: black;
      font-weight: 900;
      text-align: center;

      @media (min-width: $medium-width) {
        font-size: 50px;
      }

    }
  }
}

.main-content {
  max-width: 860px;
  margin: 0 auto;
}

.problem-contribute {
  padding: 0 1em;
  font-size: 3.3vw;

  @media (min-width: $medium-width) {
    font-size: 14px;
  }
}

.series-selector {
  padding: 0.5em;

  a, a:link {
      display: inline-block;
      padding: 0 0.5em;
      line-height: 1.5em;
      height: 1.5em;
      font-size: 4.4vw;
      margin-inline-end: 0.33em;
      margin-bottom: 0.33em;
      border-radius: 4px;
      font-weight: 500;
      background-color: $button-bg;
      text-decoration: none;
      color: $text-primary;

      @media (min-width: $medium-width) {
        font-size: 20px;
      }
  
      &.selected {
        color: $background-primary;
        background-color: $text-primary;
      }
  }
}

.links {
  font-size: 3.7vw;

  @media (min-width: $medium-width) {
    font-size: 22px;
  }

  a {
    display: inline-block;
    margin-inline-end: 0.4em;
  }
  .link-row {
    display: flex;
    align-items: center;
    margin-bottom: 0.25em;
    .label-type {
      position: relative;
      top: 0.1em;
      margin-right: 0.55em;

      &.svg {
        display: inline-block;
        height: 1em;
        svg {
          width: 1em;
          height: 1em;
          path {
            fill: $text-primary;
          }
        }
      }
    }
  }
}

.series-list {
  padding: 1rem;

  .series {
    margin-bottom: 5em;
  
    h2 {
      margin: 0;
      font-size: 5.7vw;

      @media (min-width: $medium-width) {
        font-size: 30px;
      }

    }
  
    .cars {
      font-size: 3.2vw;
      margin-bottom: 0.5em;
    
      @media (min-width: $medium-width) {
        font-size: 18px;
      }

      .car {
        display: inline-block;
        margin-inline-end: 0.4em;
  
        &:after {
          content: ', ';
        }
  
        &:last-child {
          &:after {
            content: '';
          }
        }
      }
    }

    .official-sessions {
      font-size: 5vw;
      font-weight: 600;
      display: block;
      margin-top: 1em;
      margin-bottom: 0.5em;

      @media (min-width: $medium-width) {
        font-size: 22px;
      }

    }
    
    .timeslots {
      font-size: 5.5vw;

      @media (min-width: $medium-width) {
        font-size: 28px;
      }

    }

  }
}

